.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 48px;
    justify-content: flex-start;
    align-items: center;
    margin-block: 0;
}

.content {
    display: flex;
    flex-direction: row;
}

.title {
    margin-top: calc(4 * var(--base));
    font-size: clamp(1rem, 4.5vw, 5rem);
    font-family: Hiragino Kaku Gothic Std, serif;
    color: #FEA832;
    text-shadow: 0.5vw 0.5vw 0 #AF291A;
}

.subtitle {
    margin: 1rem 0;
    font-size: clamp(0.75rem, 3vw, 3rem);
    font-family: Archer, serif;
    color: #FEA832;
    text-shadow: 0vw 0vw 0 #AF291A;
}

.text {
    margin-top: 1.5rem;
    font-size: clamp(1.6vw, 1.6vw, 4rem);
    font-family: Helvetica, serif;
    font-weight: 100;
    color: #000;
    text-wrap: auto;
    line-height: 2;
    text-align: justify;
}

.textItalic {
    font-size: clamp(1.6vw, 1.6vw, 4rem);
    font-family: Helvetica, serif;
    font-style: italic;
    font-weight: 100;
    color: #000;
    text-wrap: auto;
    line-height: 2;
    text-align: justify;
}

.textCenter {
    text-align: center;
}

@media (orientation: portrait) {
    .container {
        flex-direction: column;
        padding: 0 16px;
    }

    .text {
        font-size: clamp(2vw, 3vw, 4rem);
    }

    .textItalic {
        font-size: clamp(2vw, 3vw, 4rem);
    }
    .content {
        flex-direction: column;
        justify-items: flex-start;
    }
}